import React from 'react';
import styles from './UserCard.module.css';
import noImage from './../../../assets/images/noImage.png'

const UserCard = ({ photo, name, position, email, phone }) => {
  const addDefaultSrc = (ev) => {
    ev.target.src = noImage;
  }
  return (
    <li className={styles.card}>
      <img onError={addDefaultSrc} className={styles.photo} src={photo} alt={name} />
      <h3 className={styles.name}>{name}</h3>
      <p className={styles.paragraph}>{position}</p>
      <p className={styles.paragraph}>{email}</p>
      <p className={styles.paragraph}>{phone}</p>
    </li>
  );
};

export default UserCard;
