import React, { Component } from 'react';
import styles from './InputFile.module.css';

class InputFile extends Component {
  constructor(props) {
    super(props);
    this.fileInput = React.createRef();
  }

  state = {
    file: '',
    fileUrl: '',
  };

  openFile = (file) => {
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = () => {
      this.setState({ fileUrl: reader.result })
    }

  }
  onImgLoad = ({ target: img }) => {
    const { file } = this.state;
    this.props.form.setFieldValue('photo', ({
      file,
      dimensions: {
        height: img.naturalHeight,
        width: img.naturalWidth
      }
    }));
  }

  handleChange = event => {
    event.preventDefault();
    const file = this.fileInput.current.files[0];
    if (file) {
      this.props.form.setFieldValue('photo', { file });
      this.setState({ file });
      this.openFile(file);
    } else {
      this.props.form.setFieldValue('photo', null);
      this.setState({ fileName: null });
    }
  };

  handleBlur = () => {
    this.props.onBlur('photo', true);
  };

  render() {
    const { file } = this.state;
    const { touched, error } = this.props;

    return (
      <>
        <img className={styles.previewImg} onLoad={this.onImgLoad} src={this.state.fileUrl} alt=""></img>
        <p>Photo</p>
        <input
          className={styles.inputfile}
          onChange={this.handleChange}
          onBlur={this.handleBlur}
          id="file"
          type="file"
          ref={this.fileInput}
        />
        <label
          className={touched ? (!error ? styles.customFileTouched : styles.error) : styles.customFile}
          tabIndex="0"
          htmlFor="file"
        >
          <span className={file ? styles.filename : styles.placeholder}>
            {touched ? (file.name || 'No file chosen') : 'Upload your photo'}
          </span>
          <span className={styles.browseButton}>Browse</span>
        </label>
      </>
    );
  }
}

export default InputFile;
