import React, {
  Component
} from 'react';
import 'normalize.css';
import PropTypes from 'prop-types';
import {
  getUsers,
  getPositions,
  postUser
} from '../services/api';
import Header from './Header/Header';
import Heading from './Heading/Heading';
import About from './About/About';
import Users from './Users/Users';
import RegistrationForm from './RegistrationForm/RegistrationForm';
import Footer from './Footer/Footer';

class App extends Component {
  static propTypes = {};

  state = {
    users: null,
    token: null,
    page: 0,
    positions: null,
  };

  async componentDidMount() {
    try {
      this.loadUsers();
      const positions = await getPositions();
      this.setState({
        positions,
      });
    } catch (err) {
      console.log(err);
    };
  };
  
  loadUsers = async () => {
    try {
      const users = await getUsers();
      this.setState({
        users,
        page: 1,
      });
    } catch (err) {
      console.log(err);
    };
  }

  loadMoreUsers = async () => {
    try {
      const {
        page
      } = this.state;
      const moreUsers = await getUsers(page + 1);
      this.setState(prevState => ({
        users: [...prevState.users, ...moreUsers],
        page: prevState.page + 1,
      }));
    } catch (err) {
      console.log(err);
    }
  };

  postNewUser = async (userFormData) => {
    try {
      const response = await postUser(userFormData);
      console.log(response)
      this.loadUsers()
    } catch (err) {
      console.log(err);
    }
  }

  render() {
    const {
      users,
      positions
    } = this.state;
    return (
      <>
        <Header />
        <main >
          <Heading />
          <About />
          <Users users={users}
            loadMoreUsers={this.loadMoreUsers}
          />
          <RegistrationForm postNewUser={this.postNewUser}
            positions={positions}
          />
        </main>
        <Footer />
      </>
    );
  }
}

export default App;